.card_lg_detail {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #0000000c;
  border: 0;
  border-radius: 0;
}

p{
    color:#fff;
}

.card {
    border: 0;
    border-radius: 0;
}

h4.card_small_title {
    color: #fff;
    text-transform: uppercase;
    font-size: 1rem;
    letter-spacing: 0.05em;
    margin-bottom: 1rem;
}

h2.card_title {
    font-size: 3.5rem;
    color: #fff;
    text-align: center;
    font-family: 'Prata', serif;
    letter-spacing: 0.05em;
    margin-bottom: 1.5rem;
}

.card .st_btn_primary {
    font-size: 1.2rem;
}

.card_sm_detail {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 1.5rem 1rem;
    height: auto;
}

.card_sm_detail p {
    font-size: 1rem;
    text-align: center;
    color: #fff;
    font-family: 'Prata', serif;
    letter-spacing: 0.05em;
}

.card_sm_detail h2.card_title {
    font-size: 2.4rem;
    color: #fff;
    text-align: center;
    font-family: 'Prata', serif;
    letter-spacing: 0.05em;
    margin-bottom: 1.5rem;
}

.card-height {
    height: 100%;
}


.breadcrumb { background-attachment: scroll; height:40vh!important;} 

.breadcrumb {background:url('//cdn.shopify.com/s/files/1/0075/1832/2770/files/breadcrumb_bg_image_1.jpg?v=1645622206') center top no-repeat fixed;background-size: cover;}

.breadcrumb { padding:100px 0 60px 0;  float:left;  width:100%;  height:auto;  text-align:center; position: relative;  z-index: 1; margin:0 0 80px 0; color:#fff;}

.footer{
    width:100%!important;
}

.pl-50 {
    padding-left: 50px!important;
}
.col-lg-6 {
    -ms-flex: 0 0 50%;
    flex: 0 0 50%;
    max-width: 50%;
}
.page-about-us .title-block {
    color: #fff!important;
    margin-bottom: 30px;
    font-weight: 700;
    font-size: 28px;
    font-family: Rubik,HelveticaNeue,Helvetica Neue,sans-serif;
}

h1{
    color: #fff!important;
margin-bottom:20px;
}

