}

.contact-form{
  
    max-width:40vw!important;
    margin:100px auto;
}
.send{
    padding:10px 50px;
    border:1px solid white;
    background-color: transparent;
}

textarea{width:100%;
    outline:none;
    padding:8px 16px;
    border:1px solid #e0e6e8;
    border-radius:4px;
    font-size:1.7rem;
    color:#888888;
    transition:box-shadow 0.2}


  @media screen and (max-width:600px){
